  box-sizing: border-box;
}
*,
*::after,
*::before {
  box-sizing: inherit;
  border: 0;
  padding: 0;
  margin: 0;
  font-family: system-ui, Arial, sans-serif;
  background-color: inherit;
  user-select: none;
  -webkit-tap-highlight-color: transparent;
}
header {
  position: absolute;
  width: 100dvw;
  top: 0;
  left: 0;
  z-index: 2;
  background-color: rgb(133, 60, 60);
  height: 60px;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
}
header .buttons {
  display: flex;
  gap: 15px;
  height: 100%;
}
header .buttons button {
  height: 100%;
  padding: 10px;
  color: white;
  cursor: pointer;
  font-size: medium;
}
header .buttons button.active-tab {
  background-color: white;
  color: rgb(133, 60, 60);
  border-radius: 5px;
}

main {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 20px;
  background: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  min-height: 100dvh;
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: cover;
  padding-top: 60px;
}
main::after {
  z-index: 1;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  position: absolute;
  content: "";
  background: rgba(0, 0, 0, 0.5);
}
main > * {
  z-index: 3;
}
main > h1 {
  font-size: 70px;
  text-align: center;
  width: min(900px, 90vw);
  font-family: "Black Ops One";
  color: white;
  text-shadow: 0 0 10px black;
}
main > p {
  color: rgb(255, 234, 140);
  font-weight: 900;
  font-size: x-large;
  text-shadow: 0 0 10px black;
}
main > button {
  background-color: rgb(133, 60, 60);
  color: white;
  padding: 15px 30px;
  font-size: medium;
  font-weight: 600;
  border-radius: 20px;
  cursor: pointer;
}
main > div {
  max-width: 90vw;
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 30px;
  border-radius: 10px;
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
}
main > div > div {
  display: flex;
  align-items: center;
  gap: 10px;
  background-color: transparent;
}
main > div > div > p > span {
  color: rgb(133, 60, 60);
  font-weight: 700;
  text-transform: uppercase;
}
main > div > div > img {
  width: 30px;
  height: 30px;
}

@media screen and (max-width: 500px) {
  header {
    flex-direction: column;
    gap: 20px;
    height: auto;
  }
  main {
    padding-top: 100px;
  }
  main > h1 {
    font-size: 50px;
  }
}
